*{
    padding:0;
    margin:0;
    font-family: 'Nunito', sans-serif;
    box-sizing: border-box;

}
.container{
    max-width:750px;
    margin:0 auto;
    border:1px solid black;
    min-height: 100vh;
    box-shadow: 0px 5px 50px 0px black;
}
body{
    background: rgb(157,89,246);
    background: radial-gradient(circle, rgba(157,89,246,0.7483368347338936) 0%, rgba(148,187,233,1) 100%);
    background-repeat: no-repeat;
    background-position: center;
    font-size:1.2rem;
}
header{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin:10px 0px;

}
a{
    text-decoration: none;
    color:white;
}
a:hover,
a:focus{
    color:purple;
    transform: scale(1.05);
    transition: transform 250ms;
}
a:active{
    transform: scale(1);
    transition: transform 150ms;
}
form{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom:10px;
}
form>input{
    margin:2.5px 0px;
    border:1px solid black;
}
form>input,
form>button{
    padding: 5px 0px;
    width:250px;
    font-size:1.2rem;
    border-radius: 10px;
}
form>button{
    color:white;
    background-color: purple;
    font-weight: 700;
    letter-spacing: 1px;
    border:1px solid purple;
    cursor: pointer;
}
form>button:hover,
form>button:focus{
    background-color: pink;
    border:1px solid pink;
    color:purple;
    transform: scale(1.03);
    transition: transform 250ms;
}
form>button:active{
    transform: scale(1);
}
form>input:hover,
form>input:focus{
    box-shadow: 5px 5px 15px 0px black;
}
.todoContainer{
    display: flex;
    flex-direction: column;
    margin:0 auto;
}
.todoItem{
    display: flex;
    justify-content: space-around;
    overflow-wrap: break-word;
    margin-bottom: 10px;
    border:1px solid black;
    margin-left:10px;
    margin-right:10px;
    color:white;
    box-shadow: 2.5px 5px 10px 0px black;
    padding:10px 0px;
}
.todoContainer>h3{
    text-align: center;
    margin-bottom:10px;
    font-weight: 900;
}
.todoItem>p{
    width:80%;
    font-style: italic;
    font-weight: 400;
}
.todoDone{
    align-self: center;
    background-color:inherit;
    border: none;
    font-size:1.2rem;
    cursor: pointer;
}
.todoItem:active{
    transform: scale(.99);
}
img{
    max-width:25px;
}
.hoveredTodo{
    color:green;
}

@media(min-width:550px){
    body{
        font-size:1.7rem;
    }
    form{
        flex-direction: row;
        justify-content: center;
        padding:0 10px;
        margin-bottom:20px;
    }
    form>button{
    font-size:1.5rem;
    width:150px;
    margin-left:5px;
    }
    form>input{
        font-size:1.5rem;
    }
    form>input{
        width:100%
    }
    header{
        flex-direction: row;
        justify-content: space-between;
        padding:15px 30px;
    }
    .todoContainer{
        padding:0 15px;
    }
    .todoItem{
        margin-bottom:20px;
    }
}